$bg:rgb(15, 15, 15);
.banner{
    height: 550px;
    background-color: $bg;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 3rem;
    box-sizing: border-box;

    &::after{
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: $bg;
        opacity: 0.4;
    }
    h1,p{
        color: white;
        font-size: 3rem;
        width: 50%;
        z-index: 10;
    }
    p{
        font-size: 1.3rem;
        letter-spacing: 2px;
        word-spacing: 2px;
        font-weight: 200;
    }
    
    div{
        display: flex;

        button{
            padding: 1rem;
            width: 150px;
            outline: none;
            border: none;background-color: white;
            z-index: 10;
            border-radius: 5px;
            margin: 1rem;
            font-size: 1.2rem;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor:pointer;
            transition: all 0.5s ease-in-out;
            
            &:nth-child(odd){
                background-color: white;
                color: black;
            }
            &:nth-child(even){
                background-color: rgb(112, 112, 112);
                color: whitesmoke;
            }
            svg{
                font-size: 1.7rem;
                margin: 0 10px ;
            }
            &:hover{
                transform: scale(1.03);
                opacity: 0.8;
            }
        }

    }
    
}
.row{
    padding: 1rem;
    background-color: $bg;
    h2{
        font-weight: 400;
    }
    div{
        display: flex;
        overflow-x: auto;
    }

}
.card{
    width: 180px;
    height: 250px;
    margin: 0.3rem;
    cursor: pointer;
    transition: transform 0.5s ease-in-out;
    &:hover{
        transform: translateY(-10px) scale(1.03);
    }
}

.genrebox{
    overflow-x: auto;
    display: flex;
    height: 100px;
    background-color: $bg;

    a{
        color: black;
        background-color: white;
        padding: 1rem 0;
        border-radius: 4px;
        margin: 1rem;
        min-width: 10rem;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.5sec;
    &:hover{
        background-color: rgb(224, 220, 220);
    }
    }
}


